body {
  margin: 0;
  padding: 0;
  font-weight: 300;
  font-size: 16px;
  line-height: 1.38;
  font-family: "BloggerSans", "Open Sans", sans-serif;
  color: #fff;
  background-color: #161c35;
}
@media (min-width: 768px) {
  body {
    font-size: 28px;
    line-height: 1.18;
    background-color: #161c35;
