@media (min-width: 1440px) {
  .site-list {
    display: flex;
    flex-wrap: wrap;
    list-style: none;
    margin-top: 39px;
  }
  .site-list__item {
    margin-right: 25px;
    overflow: hidden;
  }
  .site-list__link {
    display: inline-block;
    position: relative;
    text-transform: uppercase;
    font-size: 20px;
    font-weight: 500;
    line-height: 1.5;
    color: #fff;
    padding: 0 4px;
    transition: transform 0.3s;
  }
  .site-list__link::before {
    content: attr(data-hover);
    position: absolute;
    top: 100%;
    font-weight: 700;
  }
  .site-list__link:hover {
    color: #ffd74b;
    transform: translateY(-100%);
    cursor: pointer;
  }
  .site-list__link:active {
    opacity: 0.3;
  }
  .site-list::before {
    position: relative;
    top: 14px;
    right: 40px;
    content: "";
    width: 60px;
    height: 2px;
    border-radius: 1px;
    background-color: #fff;
  }
}
.user-list {
  display: flex;
  list-style: none;
  position: absolute;
  top: 442px;
  left: 50%;
  transform: translateX(-50%);
  padding: 0;
  z-index: 1;
}
.user-list__item {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #ff8d30;
  border-radius: 20px;
  width: 40px;
  height: 40px;
}
.user-list__item:nth-child(2) {
  background-color: #ffbd30;
}
.user-list__item:nth-child(2):hover {
  background-color: #ff8d30;
}
.user-list__item:nth-child(2):active {
  background-color: #ff8d30;
}
.user-list__item:nth-child(3) {
  width: 192px;
  background-color: #ffd74b;
}
.user-list__item:nth-child(3):hover {
  background-color: #f26926;
}
.user-list__item:nth-child(3):active {
  background-color: #f26926;
}
.user-list__item:hover {
  background-color: #ffbd30;
}
.user-list__item:active {
  background-color: #ffbd30;
}
.user-list__link {
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 17px;
  font-weight: bold;
  line-height: 1.76;
  text-transform: uppercase;
  color: #161c35;
}
.user-list__link:active {
  opacity: 0.3;
}
.user-list__icon {
  fill: #161c35;
}
@media (min-width: 768px) {
  .user-list {
    position: static;
    transform: translateX(0);
  }
  .user-list__item {
    width: 50px;
    height: 50px;
    border-radius: 50%;
  }
  .user-list__item:last-child {
    width: 192px;
    height: 50px;
    border-radius: 25px;
  }
  .user-list__item:not(:last-child) {
    display: none;
  }
  .user-list__link {
    font-size: 20px;
    line-height: 1.5;
  }
}
@media (min-width: 1440px) {
  .user-list__item:not(:last-child) {
    display: flex;
  }
  .user-list__item:first-child {
    background-color: #ff8d30;
  }
  .user-list__item:first-child:hover {
    background-color: #f26926;
  }
  .user-list__item:nth-child(2) {
    background-color: #ffbd30;
  }
  .user-list__item:nth-child(2):hover {
    background-color: #ff8d30;
  }
  .user-list__item:last-child:hover {
    background-color: #ffbd30;
  }
  .user-list__link:active {
    opacity: 0.3;
  }
}
