@font-face {
  font-family: "BloggerSans";
  src: local("Blogger Sans Light"),
    url("../fonts/bloggersans-light.woff2") format("woff2"),
    url("../fonts/bloggersans-lightitalic.woff") format("woff");
  font-weight: 300;
  font-style: normal;
  font-display: swap;
}

